import React from 'react';

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'));
const Chart = React.lazy(() => import('./views/charts/Charts'));
const Info = React.lazy(() => import('./views/notifications/alerts/Alerts'));
const Jobs = React.lazy(() => import('./views/base/jobs/jobs'));
const Categories = React.lazy(() => import('./views/base/categories/categories'));
const insertCategory = React.lazy(() => import('./views/base/categories/insert-category'));
const Services = React.lazy(() => import('./views/base/services/services'));
const InsertAService= React.lazy(() => import('./views/base/services/insert-service'));
const Facilities = React.lazy(() => import('./views/base/facilities/facilities'));
const InsertAFacilitie= React.lazy(() => import('./views/base/facilities/insert-facilitie'));
const Comments = React.lazy(() => import('./views/base/comments/comments'));

const routes = [
  { path: '/', exact: true, name: 'خانه' },
  { path: '/dashboard', name: 'داشبورد', component: Dashboard },
  { path: '/chart', name: 'نمودار', component: Chart },
  { path: '/info', name: 'اطلاعات', component: Info },
  { path: '/base/jobs', name: 'کاربران', component: Jobs },
  { path: '/base/categories', name: 'دسته بندی ها', component: Categories },
  { path: '/base/insert-category', name: 'ایجاد دسته بندی', component: insertCategory },
  { path: '/base/services', name: 'خدمات', component: Services },
  { path: '/base/insert-service', name: 'ثبت خدمت جدید', component: InsertAService },
  { path: '/base/facilities', name: 'سرویس ها', component: Facilities },
  { path: '/base/insert-facilitie', name: 'ثبت سرویس جدید', component: InsertAFacilitie },
  { path: '/base/comments', name: 'کاربران', component: Comments },
];

export default routes;